          comment: '<a href="https://arxiv.org/abs/1603.09440" target="_blank">1603.09440</a>や<a href="https://arxiv.org/abs/1702.08659" target="_blank">1702.08659</a>では磁気多極子秩序によって対称性が破れた相の超伝導状態に注目していたが，今回はむしろ"破れる寸前"の相に注目し，臨界点近くの多極子揺らぎによってどんな超伝導状態が現れるかを研究した．2015年の<a href="https://journals.aps.org/prl/abstract/10.1103/PhysRevLett.115.207002" target="_blank">Kozii-Fuの研究</a>では，スピン軌道相互作用がある系において「奇パリティの電気的揺らぎによってスピン三重項超伝導が誘起される」という結果が得られていたが，磁気的揺らぎの場合や偶パリティの場合にはどうなるのかということが気になって調べ始めたのがきっかけ．Kozii-Fuではあまり議論されていなかった結晶場の効果なども考えている(特に，上記のスピン三重項超伝導は等方的な場合にはTcがかなり低いのに対し，結晶場を入れると高くなるケースがあることは重要な結果)．最初はコンパクトな論文にしようと思っていたのだが，分類表や候補物質を盛り込んでいるうちに妙に長くなってしまった．',
