        {
          title: 'Classification of topological crystalline superconducting nodes on high-symmetry lines: Point nodes, line nodes, and Bogoliubov Fermi surfaces',
          author: '<u>Shuntaro Sumita</u>, Takuya Nomoto, Ken Shiozaki, and Youichi Yanase',
          arxiv: '1811.08627',
          journal: 'Phys. Rev. B',
          volume: '99',
          page: '134513',
          year: 2019,
          url: 'https://journals.aps.org/prb/abstract/10.1103/PhysRevB.99.134513',
          comment: '<a href="https://journals.aps.org/prl/abstract/10.1103/PhysRevLett.118.207001" target="_blank">Micklitz-Norman (2017)</a>や<a href="https://arxiv.org/abs/1801.03293" target="_blank">1801.03293</a>ではブリルアンゾーンの高対称面上の超伝導ギャップ構造を群論的に分類し，線ノードが現れる条件を特定した．さらに，小林氏との共同研究(1711.06421)により，この線ノードはすべて0次元あるいは1次元のトポロジカル数によって特徴付けられることがClifford代数の知識を用いて明らかになった．一方で，ブリルアンゾーンの高対称軸上のギャップ構造については，群論的な分類は明らかとなっているが，それによって分かるノードがトポロジカル数によって特徴付けられているかは自明ではない．そこで，このような軸上の既約表現がもつemergentな対称性(emergent AZ class)に注目し，トポロジカル数の存在を明らかにした．結果として，群論的分類によって特定されるノードはすべてトポロジカル数によっても特徴付けられることが分かった．さらに論文では，系全体の対称性にも注目し，そのようなノードは点ノードなのか，あるいは線ノードや面ノードの一部なのかということも明らかにした．結構職人的な仕事になってしまったが個人的にはお気に入りの論文．',
