        {
          title: 'Unconventional superconducting gap structure protected by space group symmetry',
          author: '<u>Shuntaro Sumita</u> and Youichi Yanase',
          arxiv: '1801.03293',
          journal: 'Phys. Rev. B',
          volume: '97',
          page: '134512',
          year: 2018,
          url: 'https://journals.aps.org/prb/abstract/10.1103/PhysRevB.97.134512',
          comment: '「超伝導対称性の分類」は超伝導の研究分野において重要な課題の一つであるが，その手法の一つとして，<a href="https://journals.aps.org/rmp/abstract/10.1103/RevModPhys.63.239" target="_blank">Sigrist-Uedaのレビュー(1991)</a>で有名な点群の既約表現による超伝導秩序変数の分類が挙げられる．しかし近年，この分類法によって導かれる超伝導ギャップ構造(= Bogoliubov準粒子スペクトルの励起エネルギー)は正しい結果を与えない場合があることが分かってきており，その代わりにブリルアンゾーンの各k点で許されるクーパー対波動関数の対称性を群論を用いて計算する厳密な超伝導ギャップの分類が提案されている．論文では，この厳密な手法を用いることで，ブリルアンゾーン内の高対称面(鏡映面または映進面)上に現れる線ノードの包括的な分類を行った．系に非共型対称性がある場合には，ブリルアンゾーン境界で(Sigrist-Uedaの分類では現れない)非自明な線ノードが現れうることがいくつかの例で知られていたが，今回の分類によってそのような非自明な現象が起こる条件を求めることができた．実はそのような研究は<a href="https://journals.aps.org/prl/abstract/10.1103/PhysRevLett.118.207001" target="_blank">Micklitz-Norman (2017)</a>によって既になされていたが，我々の論文ではさらに空間反転をもちかつprimitiveな59個の空間群に対して上記の条件を当てはめ，分類表を作成した．また一方，ブリルアンゾーン内の高対称軸(n回回転軸)上のノードの分類については先行研究が少なかったため，共型な場合に限って分類を行った．結果として，3回あるいは6回回転対称性を持つ軸上では正常Bloch状態の全角運動量jzに依存したポイントノードの構造が現れることを見出した．また，UPt<sub>3</sub>, UBe<sub>13</sub>, SrPtAsといった現実の物質においてもこの非自明なギャップ構造の存在を示した．',
          bibtex:
          `@article{PhysRevB.97.134512,
  title = {Unconventional superconducting gap structure protected by space group symmetry},
  author = {Sumita, Shuntaro and Yanase, Youichi},
  journal = {Phys. Rev. B},
  volume = {97},
  issue = {13},
  pages = {134512},
  numpages = {16},
  year = {2018},
  month = {Apr},
  publisher = {American Physical Society},
  doi = {10.1103/PhysRevB.97.134512},
  url = {https://link.aps.org/doi/10.1103/PhysRevB.97.134512}
}`
