          year: 2017,
          url: 'https://journals.aps.org/prl/abstract/10.1103/PhysRevLett.119.027001',
          comment: 'Sr<sub>2</sub>IrO<sub>4</sub>は銅酸化物高温超伝導体とよく似た性質を持つ層状ペロブスカイト構造の絶縁体であり，電子ドープなどで超伝導が発現することが期待されている．また，面内で傾いた反強磁性構造を持つことが知られており，そのスタッキングの仕方が違う−++−, ++++, −+−+という3つ磁気構造が報告されている．このような背景から，磁気構造の中でも−++−と−+−+という2つに注目し，その秩序と共存する超伝導の性質を群論によるギャップ関数の解析および有効モデルを用いた数値計算といった2つの手法を組み合わせて調査した．その結果，−++−状態では磁気的な非共型対称性に守られた非自明な超伝導ギャップ構造が現れ，−+−+状態では磁場を必要としないFFLO超伝導が発現することを明らかにした．',
