          volume: '93',
          page: '224507',
          year: 2016,
          url: 'https://journals.aps.org/prb/abstract/10.1103/PhysRevB.93.224507',
          comment: '磁場中の超伝導状態はこれまでに多く議論されてきたテーマである．一方で，固体内での多極子自由度といったものが近年の凝縮系物理学で注目を集めている．そこで，「磁気多極子と共存する超伝導状態」の新奇な性質を調査することを目的とし，1次元ジグザグ格子という簡単なモデルを用いて平均場理論で解析を行った．その結果，磁気単極子状態では通常のBCS超伝導であるが，磁気双極子状態と磁気四極子状態ではそれぞれPDW超伝導・FFLO超伝導が発現するという非自明な性質を見出すことができた．',
